export default {
	props: {
		showPayment: {
			type: Boolean,
			value: false
		},
		icon: {
			type: String,
			value: 'help'
		},
		title: {
			type: String,
			value: '支付'
		},
		description: {
			type: String,
			value: null
		},
		orderIndividual: {
			type: Object,
			value: null
		}
	},
	data() {
		return {
			isBalanceInsufficient: false
		};
	},
	methods: {
		hideModal() {
			console.log('hiding')
			this.showPayment = false
			this.$emit("hideModal")
		},
		pay() {
			let that = this
			this.$pin.request('POST', '/commons/payment/unified-pay', {
					orderIndividualId: this.orderIndividual.id,
					paymentType: 'BALANCE'
				},
				successData => {
					if (successData.code == that.$pin.code.success) {
						uni.showToast({
							title: '支付成功'
						})
						this.$emit("paymentSuccess")
					} else if (successData.code == that.$pin.code.repeatPayment) {
						// 重复支付
						uni.showToast({
							title: '您已支付过此订单',
							icon: 'none'
						})
						this.$emit("paymentSuccess")
					} else if (successData.code == that.$pin.code.insufficientBalance) {
						// 余额不足
						that.icon = 'error'
						that.isBalanceInsufficient = true
						this.$emit("paymentFail")
					}
				},
				failData => {
					uni.showToast({
						title: '支付失败，请检查设备网络状态并重试。',
						icon: 'none'
					})
				})
		}
	}
}
